@font-face {
  font-family: LEngineer-Regular;
  src: url(/idealnemoloko-react/static/media/LEngineer-Regular.8a1b91a8.otf);
}
@font-face {
  font-family: Proxima-Nova-Semibold;
  src: url(/idealnemoloko-react/static/media/Proxima-Nova-Regular.410504d4.otf);
}
@font-face {
  font-family: Proxima-Nova-Bold;
  src: url(/idealnemoloko-react/static/media/Proxima-Nova-Bold.62d4d7d3.otf);
}

html {
  height: 100%;
  overflow-x: hidden;
  overflow-y: hidden;
}
body {
  height: 100%;
  margin: 0;
  padding: 0;
}
#root {
  height: 100%;
}
#root > div {
  height: 100%;
}

.logo {
  position: absolute;
  top: 5vh;
  left: 11vw;
  margin-left: -3vh;
  height: 14vh;
  z-index: 2;
  cursor: unset;
}
.logo-hidden {
  cursor: pointer;
}

.splash-left {
  position: fixed;
  right: 81vw;
  bottom: -10%;
  height: 85%;
  z-index: 1;
}
.splash-right {
  position: fixed;
  right: 45vw;
  top: -38%;
  height: 80%;
  z-index: 1;
}

.left-bar {
  position: absolute;
  top: 5vh;
  right: 12vw;
  z-index: 2;

  font-family: Proxima-Nova-Semibold;
  font-size: 1.7vh;
}
.left-bar > span {
  margin-right: 2vw;
}
.left-bar a {
  text-decoration: none;
  color: #ffffff;
}
.left-bar a:hover {
  color: #ffffff;
  /* text-shadow: 0px 0px 4px #555555cc; */
}

.social {
  position: absolute;
  top: 4.4vh;
  right: 1.5vw;
  z-index: 2;
}
.social a {
  display: inline-block;
  width: 3vh;
  height: 3vh;
  margin-right: 1.5vw;
}
.social img {
  width: 100%;
  height: 100%;
}

.main-page {
  height: 100%;
}
.swiper-container, .swiper-wrapper, .swiper-slide {
  height: 100%;
  z-index: 0;
}

.swiper-slide-left {
  position: absolute;
  height: 100%;
  width: 100%;
  box-sizing: border-box;
  background-size: 50% 100%;
  background-repeat: no-repeat;
}

.swiper-slide-left > img {
  position: absolute;
  bottom: 5vh;
  right: 5vw;
  height: 25vh;
  width: 25vh;
}

.swiper-slide-right {
  position: absolute;
  right: 0;
  height: 100%;
  width: 57%;
  box-sizing: border-box;
}

.swiper-slide-right-title {
  margin-top: 19vh;
  margin-left: 12vw;
  height: 5vh;

  text-align: center;
  font-family: LEngineer-Regular;
  font-size: 3.5vh;
  -webkit-font-smoothing: antialiased;
  color: #7d4526;
  cursor: pointer;
}
.swiper-slide-right-title-hidden {
  cursor: unset;
}

.swiper-slide-right-img {
  height: 70%;
  text-align: center;
  cursor: pointer;
}
.swiper-slide-right-img-hidden {
  cursor: unset;
}
.swiper-slide-right-img img {
  position: absolute;
  right: 43%;
  height: 70vh;

  -webkit-transform: translateX(50%);

          transform: translateX(50%);
}

.slide-vivsyane {
  background-color: #ffd889;
  /* background-color: #ffe5ab; */
}

.slide-grechane {
  background-color: #deb297;
  /* background-color: #eaccb8; */
}
.swiper-container-vertical > .swiper-pagination-bullets {
  right: 2vw;
  -webkit-transition: right 0.5s ease;
  transition: right 0.5s ease;
  -webkit-transition-delay: 0.5s;
          transition-delay: 0.5s;
}
.swiper-container-vertical > .bullets-hidden {
  right: -6vw;
  -webkit-transition: right 1s ease;
  transition: right 1s ease;
  -webkit-transition-delay: 0.5s;
          transition-delay: 0.5s;
}
.swiper-pagination-bullet {
  height: 2vh;
  width: 2vh;
  background: none;
  border: 0.5vh solid white;
  opacity: 1;
}
.swiper-pagination-bullet-active {
  background: white;
}
.curtain {
  position: absolute;
  left: -48%;
  top: 0;

  width: 100%;
  height: 100%;

  background: #bcffd0;
  /* background-image: radial-gradient(circle, #bcffd0, #b3ffca, #a9ffc4, #9fffbe, #95ffb8); */
  box-shadow: 5px 0px 50px 10px rgba(0,0,0,0.3);
  -webkit-transform: skewX(-10deg);
          transform: skewX(-10deg);

  -webkit-transition: left 0.5s ease;

  transition: left 0.5s ease;
  -webkit-transition-delay: 0.5s;
          transition-delay: 0.5s;
}
.curtain-hidden {
  left: -120%;
  -webkit-transition: left 1s ease;
  transition: left 1s ease;
  -webkit-transition-delay: 0.5s;
          transition-delay: 0.5s;
}

.curtain-glow {
  position: absolute;
  right: 25%;
  top: 50%;
  width: 50vw;
  height: 80vh;
  border-radius: 50%;
  box-shadow: 0 0 9vw 6vw #000000, inset 1px 0 9vw 6vw #000000;
  opacity: 0.04;
  -webkit-transform: translate(40%, -50%) skewX(10deg);
          transform: translate(40%, -50%) skewX(10deg);
}

.leaves {
  position: absolute;
  right: 5vw;
  bottom: 6vh;
  height: 18vh;
  z-index: 1;
  -webkit-transform: skewX(10deg);
          transform: skewX(10deg);
}
.info {
  position: absolute;
  top: 24vh;
  left: 11vw;
  width: 35%;
  height: 76%;
  z-index: 2;

  opacity: 0;
  -webkit-transition: opacity 0.5s ease;
  transition: opacity 0.5s ease;
}
.info-hidden {
  opacity: 1;
  -webkit-transition: opacity 1s ease;
  transition: opacity 1s ease;
  -webkit-transition-delay: 1s;
          transition-delay: 1s;
}

.info-header > * {
  margin: 0;

  font-family: LEngineer-Regular;
  font-size: 4vh;
  -webkit-font-smoothing: antialiased;
  font-weight: lighter;
  color: #75321f;
}

.info-body {
  margin-top: 4vh;
  font-family: Proxima-Nova-Semibold;
}
.info-body-title{
  margin: 0;
  color: #75321f;
  font-size: 1.7vh;
}
.info-body-subtitle {
  margin: 0;
  margin-top: 0.5vh;
  color: #75321f;
  font-size: 1.5vh;
}
.info-body-description {
  margin-bottom: 0;
  font-size: 1.4vh;
  line-height: 2.3vh;
}
.info-body-list-title {
  margin: 0;
  font-size: 1.4vh;
  line-height: 2.4vh;
}
.info-body-list {
  margin: 0;
  padding-left: 2vh;
}
.info-body-list > li {
  font-size: 1.4vh;
  line-height: 2.3vh;
}
.info-body-list > li > * {
  margin-left: -0.5vh;
  font-size: 1.4vh;
  line-height: 2.3vh;
}
.info-body-finally {
  margin: 0;
  font-size: 1.4vh;
  line-height: 2.4vh;
}

.info-footer {
  margin-top: 2vh;
}
.info-footer-table-title {
  margin-top: 0;
  margin-bottom: 1vh;
  font-family: LEngineer-Regular;
  font-size: 3vh;
  -webkit-font-smoothing: antialiased;
  font-weight: lighter;
  color: #75321f;
}

.info-footer-table-head {
  width: 30vh;
  margin-bottom: 0.5vh;
  font-family: Proxima-Nova-Semibold;
  background: #ffffff77;
}
.info-footer-table-body {
  width: 30vh;
  font-family: Proxima-Nova-Semibold;
  background: #ffffff77;
}
.info-footer-table-col {
  display: inline-block;
  width: 55%;
}
.info-footer-table-col:first-child {
  width: 45%;
}
.info-footer-table-col > h6 {
  margin: 0.8vh;
  font-size: 1.4vh;
}
.info-footer-table-col > div {
  margin: 0.5vh 0.8vh;
  font-size: 1.4vh;
}
.main {
  position: absolute;
  top: 24vh;
  left: 11vw;
  width: 35%;
  height: 76%;
  z-index: 2;

  opacity: 1;
  -webkit-transition: opacity 1s ease;
  transition: opacity 1s ease;
  -webkit-transition-delay: 1s;
          transition-delay: 1s;
}
.main-hidden {
  opacity: 0;
  -webkit-transition: opacity 0.5s ease;
  transition: opacity 0.5s ease;
}

.main-header {
  height: 6vh;
}
.main-header > * {
  font-family: LEngineer-Regular;
  font-size: 3.5vh;
  -webkit-font-smoothing: antialiased;
  font-weight: lighter;
  color: #75321f;
}
.main-body {
  height: 40%;
  margin-top: 4vh;
  margin-right: 2%;
  z-index: 2;

  font-family: Proxima-Nova-Semibold;
  font-size: 1.8vh;
  line-height: 2.7vh;
  color: #266336;
}
.why-page {
  height: 100%;
  width: 100%;
  background-color: #e2e4e3;
}
.why-swiper-container {
  height: 100%;
  z-index: 1;
}
.why-swiper-container > .swiper-wrapper {
  z-index: 1;
}

.why-left {
  position: absolute;
  top: 20vh;
  left: 11vw;
  width: 35%;
  height: 76%;
  z-index: 2;
  color: #693428;

  opacity: 1;
  -webkit-transition: opacity 1s ease;
  transition: opacity 1s ease;
  -webkit-transition-delay: 1s;
          transition-delay: 1s;
}

.why-header {
  height: 6vh;
}
.why-header > * {
  font-family: LEngineer-Regular;
  font-size: 4vh;
  -webkit-font-smoothing: antialiased;
  font-weight: lighter;
  color: #75321f;
}
.why-body {
  height: 40%;
  margin-top: 4vh;
  z-index: 2;

  font-family: Proxima-Nova-Semibold;
}

.why-body-title{
  margin: 0;
  font-size: 2.1vh;
  text-transform: uppercase;
  font-family: Proxima-Nova-Bold;
  -webkit-font-smoothing: antialiased;
}
.why-body-description {
  margin-top: 3vh;
  font-size: 1.5vh;
  line-height: 2.6vh;
}

.why-img-center {
  position: absolute;
  right: 25vw;
  height: 100%;
  -webkit-transform: translateX(50%);
          transform: translateX(50%);

  z-index: 0;
}
.why-img-right {
  position: absolute;
  right: 0;
  height: 100%;
}
